.wap-hero-1 {
    background-image: url(https://res.cloudinary.com/ds8xkm0ue/image/upload/v1663319227/tomas-williams-Ic_lvVv3Dz0-unsplash_1_mdvdik.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 450px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: Monsterrat;
    color: white;


    >* {
        padding: 10px;
    }

    .hero-item-1 {
        font-size: 1.2rem;
        font-weight: 300;
        cursor: text;

    
    }

    .hero-item-2 {
        font-size: 3.4rem;
        padding: 10px;
        cursor: text;
        text-decoration: none;
        font-weight: 300;
        font-style: normal;

    }

    .hero-item-3 {

        color: #fff;
        background: none;
        border: none;
        background-color: transparent;

        // border-radius: 0.4rem;
        // cursor: pointer;
        // font-weight: 300;
    }

    // .hero-item-3:hover {
    //     opacity: 0.6;
    // }

    @include for-mobile-layout {
        .hero-item-2 {
            font-size: 2rem;
        }

        .hero-item-3 {
            padding: .8rem 1.2rem;

        }
    }

    @include for-narrow-layout {
        .hero-item-3 {
            padding: 1.2rem 2.004rem;
        }
    }

    @include for-mid-layout {
        .hero-item-2 {
            font-size: 2.5rem;
        }
    }
}

.wap-hero-2-1 {
    display: flex;
    font-family: Monsterrat;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    background-size: cover;
    font-size: 20px;

    .title{
        margin-top: 30px;
    }

    .paragraph{
        margin-bottom: 30px;
    }

    background: linear-gradient(to top right,
            #fad255 50%,
            #aae6f0 0 100%);
    // padding-inline: auto;
    padding-block: 50px;

    &:first-child {
        width: 100%;
    }


    .hero-item-1 {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        gap: 40px;
        padding: 50px;
    }

    .hero-2-image {
        margin-top: 100px;
        width: 100%;
        margin: 20px;
    }

    .title {
        font-size: 1.2rem;
        font-family: Monsterrat;
    }

    .paragraph {
        font-size: 1.1rem;
        line-height: 2rem;
    }

    @include for-mobile-layout {}

    @include for-narrow-layout {}

    @include for-mid-layout {
        flex-direction: row;
        align-items: flex-start;

        section {
            width: 50%;
            max-width: 550px;
        }

    }
}

.wap-hero-2-2 {
    display: flex;
    flex-direction: column;
    font-family: Monsterrat-thin;
    justify-content: flex-end;
    align-items: stretch;


    background: linear-gradient(to top right,
            #fbcaec 50%,
            #fad255 0 100%);



    .video {
        width: 100%;
        height: 600px;
        margin: auto;

        .video-container {
            position: relative;
            width: 100%;
            height: 100%;

            // padding: 56.25%;
            .video-player {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
            }
        }
    }

    .hero-item-1 {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        gap: 50px;
        height: 100%;
        padding: 10px;
    }

    .title {
        font-size: 1.2rem;
        font-family: Monsterrat;
    }

    .paragraph {
        font-size: 1.1rem;
        line-height: 2rem;
    }

    .sweetpies-btn {
        font-family: Monsterrat;
        background-color: #fbcaec;
        width: fit-content;

        button {
            background: none;
            border: none;
            padding: 10px 20px;
        }


        a {
            color: #4d4d4d;
            font-weight: bold;

        }

        &:hover {
            background-color: #FADF55;
            transition: 0.6s;
            cursor: pointer;
        }
    }

    @include for-mobile-layout {
        section {
            text-align: center;

        }
    }

    @include for-narrow-layout {

        .hero-item-1 {
            padding: 50px;
            gap: 20px;

            .paragraph {
                font-size: 0.8rem;
            }
        }

    }

    @include for-mid-layout {
        padding: 70px 0px;
        justify-content: center;
        flex-direction: row;

        &>* {
            margin: auto;
        }

        video {
            max-height: 600px;
            max-width: 800px;
        }

        section {
            display: flex;
            width: 50%;

            img {
                margin-inline-start: auto;
            }
        }


        .hero-item-1 {
            gap: 20px;
            font-size: 1rem;
            margin-top: 50px;
            max-height: 100%;

            .title {
                font-size: 1rem;
                padding-top: 20px;
            }

            .paragraph {
                font-size: 0.8rem;
            }

            button {
                font-size: .9rem;

            }
        }

    }

    @include for-normal-layout {
        .video {
            width: 35%;
            margin-top: 50px;
        }

        .hero-item-1 {
            width: 100%;
            font-size: 1rem;
            gap: 50px;

            .paragraph {
                font-size: 1rem;
            }
        }

    }


}

.wap-hero-3 {
    background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('../../../img/hero3/4c82981e961041ae9b1a50b5895e47ae.png');
    background-size: cover;
    background-repeat: no-repeat;
    font-size: 6px;

    .hero-text-container {
        min-width: 350px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-top: 200px;

        color: #fff;

        &>* {
            text-transform: uppercase;
        }

        .title1 {
            font-size: 4em;
            padding-inline: 20px;

            &::after,
            &::before {
                content: '-';
                padding-inline: 20px;
            }
        }

        .title2 {
            font-size: 6em;
        }
    }

    @include for-mobile-layout {}

    @include for-narrow-layout {
        background-attachment: fixed;
        font-size: 10px;

        .title1 {
            font-size: 4em;
            padding-inline: 20px;

            &::after,
            &::before {
                content: '-';
                padding-inline: 20px;
            }
        }

        .hero-text-container {
            padding-bottom: 500px;
        }

        .title2 {
            font-size: 6em;
        }
    }

    @include for-mid-layout {
        background-attachment: fixed;
        font-size: 16px;

        .title1 {
            font-size: 4em;
            padding-inline: 20px;

            &::after,
            &::before {
                content: '-';
                padding-inline: 20px;
            }
        }

        .title2 {
            font-size: 6em;
        }
    }
}

